// -   (, )

import React, { useState, useEffect, useCallback, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../../contexts/AuthContext";
import { db } from "../../firebase";
import {
  collection,
  getDocs,
  doc,
  updateDoc,
  deleteDoc,
  query,
  where,
  orderBy,
  onSnapshot,
  serverTimestamp,
  getDoc,
  limit,
} from "firebase/firestore";
import {
  Shield,
  Users,
  School,
  AlertTriangle,
  CheckCircle,
  XCircle,
  RefreshCw,
  Activity,
  Database,
  Clock,
  TrendingUp,
  Zap,
  Eye,
  Trash2,
  UserCheck,
  UserX,
  Search,
  Filter,
  BarChart3,
  Bug,
  AlertOctagon,
  Server,
  Cpu,
  HardDrive,
  Wifi,
} from "lucide-react";
import "./SuperAdminDashboard.css";

import { logger } from "../../utils/logger";
//  
const TABS = [
  { id: "overview", label: "", icon: BarChart3 },
  { id: "pending", label: " ", icon: Clock },
  { id: "teachers", label: " ", icon: UserCheck },
  { id: "classes", label: " ", icon: School },
  { id: "monitoring", label: " ", icon: Activity },
  { id: "errors", label: " ", icon: Bug },
];

export default function SuperAdminDashboard() {
  const navigate = useNavigate();
  const { userDoc, user } = useAuth();
  const [activeTab, setActiveTab] = useState("overview");
  const [loading, setLoading] = useState(true);
  const [refreshing, setRefreshing] = useState(false);

  //  
  const [stats, setStats] = useState({
    totalTeachers: 0,
    pendingTeachers: 0,
    approvedTeachers: 0,
    totalClasses: 0,
    totalStudents: 0,
    activeUsers24h: 0,
  });
  const [pendingTeachers, setPendingTeachers] = useState([]);
  const [approvedTeachers, setApprovedTeachers] = useState([]);
  const [classes, setClasses] = useState([]);
  const [errorLogs, setErrorLogs] = useState([]);
  const [systemMetrics, setSystemMetrics] = useState({
    cpuUsage: 0,
    memoryUsage: 0,
    activeConnections: 0,
    requestsPerMinute: 0,
    avgResponseTime: 0,
    errorRate: 0,
  });

  // / 
  const [searchTerm, setSearchTerm] = useState("");
  const [filterStatus, setFilterStatus] = useState("all");

  //   
  const errorListenerRef = useRef(null);
  const metricsIntervalRef = useRef(null);

  //  
  useEffect(() => {
    if (userDoc && !userDoc.isSuperAdmin) {
      navigate("/dashboard/tasks");
    }
  }, [userDoc, navigate]);

  //  
  const loadAllData = useCallback(async () => {
    if (!userDoc?.isSuperAdmin) return;

    setLoading(true);
    try {
      await Promise.all([
        loadStats(),
        loadPendingTeachers(),
        loadApprovedTeachers(),
        loadClasses(),
        loadErrorLogs(),
      ]);
    } catch (error) {
      logger.error("  :", error);
    } finally {
      setLoading(false);
    }
  }, [userDoc]);

  //  
  const loadStats = async () => {
    try {
      //  users  () 
      // isTeacher === true  isAdmin === true  (isSuperAdmin )
      const usersRef = collection(db, "users");
      const usersSnap = await getDocs(usersRef);

      let totalTeachers = 0;
      let pending = 0;
      let approved = 0;

      usersSnap.docs.forEach((doc) => {
        const data = doc.data();
        // isSuperAdmin   
        if (data.isSuperAdmin) return;

        // isTeacher  isAdmin true   
        const isTeacher = data.isTeacher === true || data.isAdmin === true;
        if (isTeacher) {
          totalTeachers++;
          //      (isApproved  false )
          if (data.isApproved === false) {
            pending++;
          } else {
            // isApproved  true     
            approved++;
          }
        }
      });

      //   +  : users  classCode 
      const classCodes = new Set();
      let totalStudents = 0;
      const now = new Date();
      const yesterday = new Date(now.getTime() - 24 * 60 * 60 * 1000);
      let activeUsers = 0;

      usersSnap.docs.forEach((doc) => {
        const data = doc.data();
        if (data.isSuperAdmin) return;

        // 24   
        const lastActive =
          data.lastActiveAt?.toDate?.() || data.lastLoginAt?.toDate?.();
        if (lastActive && lastActive > yesterday) activeUsers++;

        const code = data.classCode;
        if (!code || code === "") return;

        //   
        classCodes.add(code);

        //     
        const isTeacher = data.isTeacher === true || data.isAdmin === true;
        if (!isTeacher) totalStudents++;
      });

      setStats({
        totalTeachers,
        pendingTeachers: pending,
        approvedTeachers: approved,
        totalClasses: classCodes.size,
        totalStudents,
        activeUsers24h: activeUsers,
      });
    } catch (error) {
      logger.error("  :", error);
    }
  };

  //    
  const loadPendingTeachers = async () => {
    try {
      //   users   
      // Firestore OR   isTeacher || isAdmin   
      const usersRef = collection(db, "users");
      const snapshot = await getDocs(usersRef);

      const pending = snapshot.docs
        .map((doc) => ({ id: doc.id, ...doc.data() }))
        .filter((user) => {
          // isSuperAdmin   
          if (user.isSuperAdmin) return false;
          // isTeacher  isAdmin true   
          const isTeacher = user.isTeacher === true || user.isAdmin === true;
          //  : isApproved  false 
          return isTeacher && user.isApproved === false;
        })
        .sort((a, b) => {
          const aTime = a.createdAt?.toDate?.() || new Date(0);
          const bTime = b.createdAt?.toDate?.() || new Date(0);
          return bTime - aTime;
        });

      setPendingTeachers(pending);
    } catch (error) {
      logger.error("    :", error);
      setPendingTeachers([]);
    }
  };

  //   
  const loadApprovedTeachers = async () => {
    try {
      //   users   
      // Firestore OR   isTeacher || isAdmin   
      const usersRef = collection(db, "users");
      const snapshot = await getDocs(usersRef);

      const approved = snapshot.docs
        .map((doc) => ({ id: doc.id, ...doc.data() }))
        .filter((user) => {
          // isSuperAdmin   
          if (user.isSuperAdmin) return false;
          // isTeacher  isAdmin true   
          const isTeacher = user.isTeacher === true || user.isAdmin === true;
          //  : isApproved false   (true undefined)
          //   isApproved   undefined   
          return isTeacher && user.isApproved !== false;
        })
        .sort((a, b) => {
          const aName = a.name || "";
          const bName = b.name || "";
          return aName.localeCompare(bName, "ko");
        });

      setApprovedTeachers(approved);
    } catch (error) {
      logger.error("   :", error);
      setApprovedTeachers([]);
    }
  };

  //   
  //    users    users  
  const loadClasses = async () => {
    try {
      logger.log("[SuperAdmin]    ...");

      // users    
      const usersRef = collection(db, "users");
      const usersSnap = await getDocs(usersRef);

      // classCode  
      const classMap = new Map(); // classCode -> { students: [], teacher: null }

      usersSnap.docs.forEach((userDoc) => {
        const data = userDoc.data();
        const classCode = data.classCode;

        // SuperAdmin classCode  
        if (data.isSuperAdmin || !classCode || classCode === "") {
          return;
        }

        if (!classMap.has(classCode)) {
          classMap.set(classCode, { students: [], teacher: null });
        }

        const classInfo = classMap.get(classCode);

        // isAdmin  isTeacher   
        if (data.isAdmin || data.isTeacher) {
          classInfo.teacher = {
            id: userDoc.id,
            name: data.name || " ",
            email: data.email || "",
          };
        } else {
          //  
          classInfo.students.push({
            id: userDoc.id,
            name: data.name,
            ...data,
          });
        }
      });

      logger.log(`[SuperAdmin]   : ${classMap.size}`);

      //    
      const classesData = [];
      for (const [classCode, classInfo] of classMap.entries()) {
        logger.log(
          `[SuperAdmin] ${classCode}: =${classInfo.teacher?.name}, =${classInfo.students.length}`,
        );

        classesData.push({
          id: classCode,
          classCode: classCode,
          className: classCode,
          studentCount: classInfo.students.length,
          totalMembers: classInfo.students.length + (classInfo.teacher ? 1 : 0),
          adminName: classInfo.teacher?.name || "",
          adminEmail: classInfo.teacher?.email || "",
          adminId: classInfo.teacher?.id || "",
        });
      }

      //    
      classesData.sort((a, b) => a.classCode.localeCompare(b.classCode, "ko"));

      logger.log(`[SuperAdmin]   :`, classesData);
      setClasses(classesData);
    } catch (error) {
      logger.error("[SuperAdmin]   :", error);
      logger.error("[SuperAdmin]  :", error.code, error.message);
      setClasses([]);
    }
  };

  //   
  const loadErrorLogs = async () => {
    try {
      const logsRef = collection(db, "errorLogs");
      const logsQuery = query(
        logsRef,
        orderBy("timestamp", "desc"),
        limit(100),
      );
      const snapshot = await getDocs(logsQuery);

      const logs = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
        timestamp: doc.data().timestamp?.toDate?.() || new Date(),
      }));

      setErrorLogs(logs);
    } catch (error) {
      logger.error("   :", error);
      //     
      setErrorLogs([]);
    }
  };

  //    
  useEffect(() => {
    if (!userDoc?.isSuperAdmin) return;

    //      (5      )
    setSystemMetrics({
      cpuUsage: 25,
      memoryUsage: 40,
      activeConnections: 15,
      requestsPerMinute: 80,
      avgResponseTime: 150,
      errorRate: 0.5,
    });

    const intervalId = metricsIntervalRef.current;
    return () => {
      if (intervalId) {
        clearInterval(intervalId);
      }
    };
  }, [userDoc]);

  //    
  useEffect(() => {
    if (!userDoc?.isSuperAdmin) return;

    try {
      const logsRef = collection(db, "errorLogs");
      const logsQuery = query(logsRef, orderBy("timestamp", "desc"), limit(50));

      errorListenerRef.current = onSnapshot(
        logsQuery,
        (snapshot) => {
          const logs = snapshot.docs.map((doc) => ({
            id: doc.id,
            ...doc.data(),
            timestamp: doc.data().timestamp?.toDate?.() || new Date(),
          }));
          setErrorLogs(logs);
        },
        (error) => {
          logger.error("   :", error);
        },
      );
    } catch (error) {
      logger.error("    :", error);
    }

    return () => {
      if (errorListenerRef.current) {
        errorListenerRef.current();
      }
    };
  }, [userDoc]);

  //   
  useEffect(() => {
    loadAllData();
  }, [loadAllData]);

  // 
  const handleRefresh = async () => {
    setRefreshing(true);
    await loadAllData();
    setRefreshing(false);
  };

  //  
  const handleApproveTeacher = async (teacherId) => {
    if (!window.confirm("  ?")) return;

    try {
      const userRef = doc(db, "users", teacherId);
      await updateDoc(userRef, {
        isApproved: true,
        approvedAt: serverTimestamp(),
        approvedBy: userDoc?.id || user?.uid,
      });

      //   
      const teacher = pendingTeachers.find((t) => t.id === teacherId);
      if (teacher) {
        setPendingTeachers((prev) => prev.filter((t) => t.id !== teacherId));
        setApprovedTeachers((prev) => [
          ...prev,
          { ...teacher, isApproved: true },
        ]);
      }

      await loadStats();
      alert(" .");
    } catch (error) {
      logger.error(" :", error);
      alert("    .");
    }
  };

  //  /
  const handleRejectTeacher = async (teacherId, teacherName) => {
    if (
      !window.confirm(
        `'${teacherName}'   ?\n    .`,
      )
    )
      return;

    try {
      const userRef = doc(db, "users", teacherId);
      await deleteDoc(userRef);

      setPendingTeachers((prev) => prev.filter((t) => t.id !== teacherId));
      await loadStats();
      alert("  .");
    } catch (error) {
      logger.error(" :", error);
      alert("    .");
    }
  };

  //   
  const handleRevokeApproval = async (teacherId, teacherName) => {
    if (!window.confirm(`'${teacherName}'   ?`))
      return;

    try {
      const userRef = doc(db, "users", teacherId);
      await updateDoc(userRef, {
        isApproved: false,
        revokedAt: serverTimestamp(),
        revokedBy: userDoc?.id || user?.uid,
      });

      const teacher = approvedTeachers.find((t) => t.id === teacherId);
      if (teacher) {
        setApprovedTeachers((prev) => prev.filter((t) => t.id !== teacherId));
        setPendingTeachers((prev) => [
          ...prev,
          { ...teacher, isApproved: false },
        ]);
      }

      await loadStats();
      alert(" .");
    } catch (error) {
      logger.error("  :", error);
      alert("    .");
    }
  };

  //   
  const handleDeleteErrorLog = async (logId) => {
    try {
      await deleteDoc(doc(db, "errorLogs", logId));
      setErrorLogs((prev) => prev.filter((log) => log.id !== logId));
    } catch (error) {
      logger.error("   :", error);
    }
  };

  //    
  const handleClearAllErrorLogs = async () => {
    if (
      !window.confirm(
        `  ${errorLogs.length}  ?`,
      )
    )
      return;
    try {
      await Promise.all(
        errorLogs.map((log) => deleteDoc(doc(db, "errorLogs", log.id))),
      );
      setErrorLogs([]);
    } catch (error) {
      logger.error("    :", error);
    }
  };

  //  
  if (!userDoc?.isSuperAdmin) {
    return (
      <div className="super-admin-dashboard">
        <div className="access-denied">
          <Shield size={64} />
          <h2>  </h2>
          <p>     .</p>
          <button onClick={() => navigate("/dashboard/tasks")}>
             
          </button>
        </div>
      </div>
    );
  }

  //  
  const filteredPendingTeachers = pendingTeachers.filter(
    (teacher) =>
      teacher.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      teacher.email?.toLowerCase().includes(searchTerm.toLowerCase()),
  );

  const filteredApprovedTeachers = approvedTeachers.filter(
    (teacher) =>
      teacher.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      teacher.email?.toLowerCase().includes(searchTerm.toLowerCase()),
  );

  const filteredClasses = classes.filter(
    (cls) =>
      cls.classCode?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      cls.adminName?.toLowerCase().includes(searchTerm.toLowerCase()),
  );

  const filteredErrorLogs = errorLogs.filter((log) => {
    const matchesSearch =
      log.message?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      log.type?.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesFilter =
      filterStatus === "all" || log.severity === filterStatus;
    return matchesSearch && matchesFilter;
  });

  return (
    <div className="super-admin-dashboard">
      {/*  */}
      <header className="sad-header">
        <div className="sad-header-left">
          <Shield className="header-icon" />
          <div>
            <h1>  </h1>
            <p>    </p>
          </div>
        </div>
        <div className="sad-header-right">
          <button
            className={`refresh-btn ${refreshing ? "spinning" : ""}`}
            onClick={handleRefresh}
            disabled={refreshing}
          >
            <RefreshCw size={20} />
            
          </button>
        </div>
      </header>

      {/*   */}
      <nav className="sad-tabs">
        {TABS.map((tab) => (
          <button
            key={tab.id}
            className={`sad-tab ${activeTab === tab.id ? "active" : ""}`}
            onClick={() => setActiveTab(tab.id)}
          >
            <tab.icon size={18} />
            <span>{tab.label}</span>
            {tab.id === "pending" && stats.pendingTeachers > 0 && (
              <span className="badge">{stats.pendingTeachers}</span>
            )}
            {tab.id === "errors" &&
              errorLogs.filter(
                (l) => l.severity === "critical" || l.severity === "error",
              ).length > 0 && (
                <span className="badge error">
                  {
                    errorLogs.filter(
                      (l) =>
                        l.severity === "critical" || l.severity === "error",
                    ).length
                  }
                </span>
              )}
          </button>
        ))}
      </nav>

      {/*   */}
      <main className="sad-content">
        {loading ? (
          <div className="loading-state">
            <RefreshCw className="spinning" size={40} />
            <p>  ...</p>
          </div>
        ) : (
          <>
            {/*   */}
            {activeTab === "overview" && (
              <div className="overview-tab">
                <div className="stats-grid">
                  <div className="stat-card teachers">
                    <div className="stat-icon">
                      <Users size={24} />
                    </div>
                    <div className="stat-info">
                      <span className="stat-value">{stats.totalTeachers}</span>
                      <span className="stat-label"> </span>
                    </div>
                  </div>

                  <div className="stat-card pending">
                    <div className="stat-icon">
                      <Clock size={24} />
                    </div>
                    <div className="stat-info">
                      <span className="stat-value">
                        {stats.pendingTeachers}
                      </span>
                      <span className="stat-label"> </span>
                    </div>
                    {stats.pendingTeachers > 0 && (
                      <button
                        className="stat-action"
                        onClick={() => setActiveTab("pending")}
                      >
                        
                      </button>
                    )}
                  </div>

                  <div className="stat-card approved">
                    <div className="stat-icon">
                      <CheckCircle size={24} />
                    </div>
                    <div className="stat-info">
                      <span className="stat-value">
                        {stats.approvedTeachers}
                      </span>
                      <span className="stat-label"> </span>
                    </div>
                  </div>

                  <div className="stat-card classes">
                    <div className="stat-icon">
                      <School size={24} />
                    </div>
                    <div className="stat-info">
                      <span className="stat-value">{stats.totalClasses}</span>
                      <span className="stat-label">  </span>
                    </div>
                  </div>

                  <div className="stat-card students">
                    <div className="stat-icon">
                      <Users size={24} />
                    </div>
                    <div className="stat-info">
                      <span className="stat-value">{stats.totalStudents}</span>
                      <span className="stat-label"> </span>
                    </div>
                  </div>

                  <div className="stat-card active">
                    <div className="stat-icon">
                      <Activity size={24} />
                    </div>
                    <div className="stat-info">
                      <span className="stat-value">{stats.activeUsers24h}</span>
                      <span className="stat-label">24  </span>
                    </div>
                  </div>
                </div>

                {/*   */}
                <div className="quick-actions">
                  <h3> </h3>
                  <div className="action-buttons">
                    {stats.pendingTeachers > 0 && (
                      <button
                        className="action-btn warning"
                        onClick={() => setActiveTab("pending")}
                      >
                        <Clock size={20} />
                          {stats.pendingTeachers} 
                      </button>
                    )}
                    <button
                      className="action-btn"
                      onClick={() => setActiveTab("monitoring")}
                    >
                      <Activity size={20} />
                        
                    </button>
                    <button
                      className="action-btn"
                      onClick={() => setActiveTab("errors")}
                    >
                      <Bug size={20} />
                        
                    </button>
                  </div>
                </div>

                {/*   */}
                <div className="recent-activity">
                  <h3>  </h3>
                  {errorLogs.length === 0 ? (
                    <p className="no-data">  .</p>
                  ) : (
                    <div className="error-list-mini">
                      {errorLogs.slice(0, 5).map((log) => (
                        <div
                          key={log.id}
                          className={`error-item ${log.severity}`}
                        >
                          <span className="error-severity">{log.severity}</span>
                          <span className="error-message">{log.message}</span>
                          <span className="error-time">
                            {log.timestamp?.toLocaleTimeString?.() ||
                              "  "}
                          </span>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            )}

            {/*    */}
            {activeTab === "pending" && (
              <div className="pending-tab">
                <div className="tab-header">
                  <h2>  </h2>
                  <div className="search-box">
                    <Search size={18} />
                    <input
                      type="text"
                      placeholder="   ..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                    />
                  </div>
                </div>

                {filteredPendingTeachers.length === 0 ? (
                  <div className="empty-state">
                    <CheckCircle size={64} />
                    <h3>    </h3>
                    <p>   .</p>
                  </div>
                ) : (
                  <div className="teacher-list">
                    {filteredPendingTeachers.map((teacher) => (
                      <div key={teacher.id} className="teacher-card pending">
                        <div className="teacher-avatar">
                          {teacher.name?.charAt(0) || "?"}
                        </div>
                        <div className="teacher-info">
                          <h4>{teacher.name || " "}</h4>
                          <p>{teacher.email}</p>
                          <span className="join-date">
                            :{" "}
                            {teacher.createdAt
                              ?.toDate?.()
                              .toLocaleDateString() || "  "}
                          </span>
                        </div>
                        <div className="teacher-actions">
                          <button
                            className="approve-btn"
                            onClick={() => handleApproveTeacher(teacher.id)}
                          >
                            <CheckCircle size={18} />
                            
                          </button>
                          <button
                            className="reject-btn"
                            onClick={() =>
                              handleRejectTeacher(teacher.id, teacher.name)
                            }
                          >
                            <XCircle size={18} />
                            
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}

            {/*    */}
            {activeTab === "teachers" && (
              <div className="teachers-tab">
                <div className="tab-header">
                  <h2>  </h2>
                  <div className="search-box">
                    <Search size={18} />
                    <input
                      type="text"
                      placeholder="   ..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                    />
                  </div>
                </div>

                {filteredApprovedTeachers.length === 0 ? (
                  <div className="empty-state">
                    <Users size={64} />
                    <h3>  </h3>
                    <p>    .</p>
                  </div>
                ) : (
                  <div className="teacher-list">
                    {filteredApprovedTeachers.map((teacher) => (
                      <div key={teacher.id} className="teacher-card approved">
                        <div className="teacher-avatar">
                          {teacher.name?.charAt(0) || "?"}
                        </div>
                        <div className="teacher-info">
                          <h4>{teacher.name || " "}</h4>
                          <p>{teacher.email}</p>
                          <span className="class-code">
                            : {teacher.classCode || ""}
                          </span>
                        </div>
                        <div className="teacher-actions">
                          <button
                            className="view-btn"
                            onClick={() => {
                              setSearchTerm(teacher.classCode || "");
                              setActiveTab("classes");
                            }}
                          >
                            <Eye size={18} />
                             
                          </button>
                          <button
                            className="revoke-btn"
                            onClick={() =>
                              handleRevokeApproval(teacher.id, teacher.name)
                            }
                          >
                            <UserX size={18} />
                             
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}

            {/*    */}
            {activeTab === "classes" && (
              <div className="classes-tab">
                <div className="tab-header">
                  <h2> </h2>
                  <div className="search-box">
                    <Search size={18} />
                    <input
                      type="text"
                      placeholder="    ..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                    />
                  </div>
                </div>

                {filteredClasses.length === 0 ? (
                  <div className="empty-state">
                    <School size={64} />
                    <h3>  </h3>
                  </div>
                ) : (
                  <div className="class-grid">
                    {filteredClasses.map((cls) => (
                      <div key={cls.id} className="class-card">
                        <div className="class-header">
                          <School size={24} />
                          <h4>{cls.classCode}</h4>
                        </div>
                        <div className="class-details">
                          <div className="detail-row">
                            <span className="label"> </span>
                            <span className="value">{cls.adminName}</span>
                          </div>
                          <div className="detail-row">
                            <span className="label"> </span>
                            <span className="value">{cls.studentCount}</span>
                          </div>
                          {cls.adminEmail && (
                            <div className="detail-row">
                              <span className="label"></span>
                              <span className="value email">
                                {cls.adminEmail}
                              </span>
                            </div>
                          )}
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}

            {/*    */}
            {activeTab === "monitoring" && (
              <div className="monitoring-tab">
                <div className="tab-header">
                  <h2> </h2>
                  <span className="live-indicator">
                    <span className="pulse"></span>
                    
                  </span>
                </div>

                <div className="metrics-grid">
                  <div className="metric-card">
                    <div className="metric-header">
                      <Cpu size={20} />
                      <span>CPU </span>
                    </div>
                    <div className="metric-value">
                      <span
                        className={systemMetrics.cpuUsage > 80 ? "warning" : ""}
                      >
                        {systemMetrics.cpuUsage.toFixed(1)}%
                      </span>
                    </div>
                    <div className="metric-bar">
                      <div
                        className={`bar-fill ${systemMetrics.cpuUsage > 80 ? "warning" : systemMetrics.cpuUsage > 60 ? "caution" : ""}`}
                        style={{ width: `${systemMetrics.cpuUsage}%` }}
                      />
                    </div>
                  </div>

                  <div className="metric-card">
                    <div className="metric-header">
                      <HardDrive size={20} />
                      <span> </span>
                    </div>
                    <div className="metric-value">
                      <span
                        className={
                          systemMetrics.memoryUsage > 80 ? "warning" : ""
                        }
                      >
                        {systemMetrics.memoryUsage.toFixed(1)}%
                      </span>
                    </div>
                    <div className="metric-bar">
                      <div
                        className={`bar-fill ${systemMetrics.memoryUsage > 80 ? "warning" : systemMetrics.memoryUsage > 60 ? "caution" : ""}`}
                        style={{ width: `${systemMetrics.memoryUsage}%` }}
                      />
                    </div>
                  </div>

                  <div className="metric-card">
                    <div className="metric-header">
                      <Wifi size={20} />
                      <span> </span>
                    </div>
                    <div className="metric-value">
                      {systemMetrics.activeConnections}
                    </div>
                  </div>

                  <div className="metric-card">
                    <div className="metric-header">
                      <Zap size={20} />
                      <span>/</span>
                    </div>
                    <div className="metric-value">
                      {systemMetrics.requestsPerMinute}
                    </div>
                  </div>

                  <div className="metric-card">
                    <div className="metric-header">
                      <Clock size={20} />
                      <span> </span>
                    </div>
                    <div className="metric-value">
                      {systemMetrics.avgResponseTime}ms
                    </div>
                  </div>

                  <div className="metric-card">
                    <div className="metric-header">
                      <AlertTriangle size={20} />
                      <span></span>
                    </div>
                    <div className="metric-value">
                      <span
                        className={systemMetrics.errorRate > 5 ? "warning" : ""}
                      >
                        {systemMetrics.errorRate.toFixed(2)}%
                      </span>
                    </div>
                  </div>
                </div>

                <div className="monitoring-info">
                  <h3> </h3>
                  <div className="status-list">
                    <div className="status-item ok">
                      <Server size={18} />
                      <span>Firebase </span>
                      <CheckCircle size={16} />
                    </div>
                    <div className="status-item ok">
                      <Database size={18} />
                      <span>Firestore </span>
                      <CheckCircle size={16} />
                    </div>
                    <div className="status-item ok">
                      <Wifi size={18} />
                      <span> </span>
                      <CheckCircle size={16} />
                    </div>
                  </div>
                </div>
              </div>
            )}

            {/*    */}
            {activeTab === "errors" && (
              <div className="errors-tab">
                <div className="tab-header">
                  <h2> </h2>
                  <div className="header-controls">
                    <div className="search-box">
                      <Search size={18} />
                      <input
                        type="text"
                        placeholder="  ..."
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                      />
                    </div>
                    <select
                      value={filterStatus}
                      onChange={(e) => setFilterStatus(e.target.value)}
                      className="filter-select"
                    >
                      <option value="all"> </option>
                      <option value="info">Info</option>
                      <option value="warning">Warning</option>
                      <option value="error">Error</option>
                      <option value="critical">Critical</option>
                    </select>
                    <button
                      className="test-error-btn"
                      onClick={handleClearAllErrorLogs}
                      title="   "
                    >
                      <Trash2 size={18} />
                       
                    </button>
                  </div>
                </div>

                {filteredErrorLogs.length === 0 ? (
                  <div className="empty-state">
                    <CheckCircle size={64} />
                    <h3>  </h3>
                    <p>   .</p>
                  </div>
                ) : (
                  <div className="error-log-list">
                    {filteredErrorLogs.map((log) => (
                      <div
                        key={log.id}
                        className={`error-log-item ${log.severity}`}
                      >
                        <div className="error-log-header">
                          <span className={`severity-badge ${log.severity}`}>
                            {log.severity === "critical" && (
                              <AlertOctagon size={14} />
                            )}
                            {log.severity === "error" && <XCircle size={14} />}
                            {log.severity === "warning" && (
                              <AlertTriangle size={14} />
                            )}
                            {log.severity === "info" && <Activity size={14} />}
                            {log.severity}
                          </span>
                          <span className="error-type">
                            {log.type || "unknown"}
                          </span>
                          <span className="error-timestamp">
                            {log.timestamp?.toLocaleString?.() || "  "}
                          </span>
                          <button
                            className="delete-log-btn"
                            onClick={() => handleDeleteErrorLog(log.id)}
                          >
                            <Trash2 size={14} />
                          </button>
                        </div>
                        <div className="error-log-body">
                          <p className="error-message">{log.message}</p>
                          {log.stack && (
                            <pre className="error-stack">{log.stack}</pre>
                          )}
                          {log.url && (
                            <span className="error-url">URL: {log.url}</span>
                          )}
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}
          </>
        )}
      </main>
    </div>
  );
}
